<template>
  <div>
    <v-data-table :headers="headers" :items="this.customer.sales" class="elevation-1">
      <template slot="items" slot-scope="props">
        <tr @click="changePage(props.item)">
          <td>{{ props.item.type }}</td>
          <td>{{ props.item.insurer }}</td>
          <td>{{props.item.value}}</td>
          <td>{{props.item.confirmed_sale}}</td>
          <td>{{props.item.date_payment}}</td>
          <td>{{props.item.date_start}}</td>
          <td>{{props.item.due}} Dias</td>
          <td>{{props.item.share}} %</td>
        </tr>
      </template>
    </v-data-table>
  </div>
</template>

<script>
const axios = require("axios");

export default {
  data: function() {
    return {
      business: [],
      headers: [
        { text: "Tipo da Aplice", value: "type" },
        { text: "Seguradora", value: "insurer" },
        { text: "Valor", value: "value" },
        { text: "Venda Confirmada?", value: "confirmed_sale" },
        { text: "Data do Pagamento", value: "date_payment" },
        { text: "Data de incio", value: "date_start" },
        { text: "Vencimento (dias)", value: "due" },
        { text: "Porcentagem de Ganhos", value: "share" }
      ]
    };
  },
  props: {
    customer: Object
  },
  methods: {
    changePage(business) {
      this.$router.push({
        name: "ShowBusiness",
        params: { business: business }
      });
    }
  },
  created() {
    var self = this;
    axios
      .get("http://localhost:3000/sales")
      .then(function(response) {
        self.business = response.data;
      })
      .catch(function(error) {
        console.log(error);
      });
  }
};
</script>

